@mixin main_button($color,$border) {
    width: 200px;
    padding: 7px;
    color: $color;
    border: $border;
    border-radius: 15px;
}
@mixin main_button_hover($background-color,$color) {
    background-color: $background-color;
    transition: 0.5s;
    color: $color;
    width: 280px;
}

.detail_product {
    width: 100%;
    display: flex;
    margin-top: 30px;
}
.detail_product_img {
    width: 50%;
    padding: 10px;
    img {
        width: 70%;
    }
}
.detail_product_text {
    width: 40%;
    margin-top: 100px;
    div {
        width: 100%;
        span {
            display: inline-block;
            color: gray;
            text-align: start;
        }
        h3 {
            padding: 10px 0;
        }
        p {
            margin: 30px 0;
        }
        button {
            @include main_button(black,1px solid black );
            background-color: white;
            display: block;
            width: 280px;
            margin: 20px auto;
            transition: 1s;
            &:hover {
                @include main_button_hover(black,white);
                width: 320px;
            }
            &:hover::after {
                content: 'Click';
                margin-left: 10px;
            }
        }
        
    }
}


@media(min-width:480px) and (max-width:767px) {
    .detail_product {
        margin-top: 20px;
    }
    .detail_product_img {
        width: 50%;
        padding: 0px;
        img {
            width: 100%;
        }
    }
    .detail_product_text {
        width: 50%;
        padding:10px;
        margin-top: 50px;
        font-size: 13px;
        div {
            width: 100%;
            h3 {
                padding: 5px 0;
            }
            p {
                margin: 10px 0;
            }
            button {
                width: 150px;
                margin: 20px auto;
                padding: 3px;
                &:hover {
                    width: 200px;
                }
            }
            
        }
    }
}

@media(min-width:768px) and (max-width:1024px) {
    .detail_product {
        width: 100%;
        display: flex;
        margin-top: 30px;
    }
    .detail_product_img {
        width: 50%;
        img {
            width: 100%;
        }
    }
    .detail_product_text {
        width: 50%;
        padding: 20px;
        margin-top: 100px;
        font-size: 15px;
        div {
            width: 100%;
            button {
                width: 230px;
                padding: 4px;
                &:hover {
                    width: 280px;
                }
            }
        }
    }
}

