
.cart_main {
    margin: 70px;
}

.cart_main_text {
    width: 100%;
    margin: 90px auto;
    display: flex;
    font-size: 15px;
    div {
        margin: 0 auto;
        width: 25%;
    }
}

.cart_product {
    width: 80%;
    margin: 0 auto;
    span {
        display: inline-block;
        margin: 0 250px;
    }   
    td {
        width: 30%;
        vertical-align: middle;
    }
    p {
        display: inline-block;
        margin-left: 10px;
        width: 160px;
    }
    button {
        width: 25px;
        background: none;
        border: 1px solid grey;
        border-radius: 100%;
        
    }
    a {
        display: inline-block;
        padding: 10px;
    }
}

.cart_buy {
    width: 90%;
    text-align: end;
    h4 {
        display: inline-block;
        margin: 0 40px;
    }
    p {
        text-align: end;
        margin: 15px 0;
    }
    button {
        width: 135px;
        padding: 5px;
        background: grey;
        border: none;
        border-radius: 5px;
        color: white;
    }
}



@media(min-width:480px) and (max-width:767px) {
    .cart_main {
        margin: 30px;
    }
    .cart_main_text {
        display: none;
    }
}
