*{
    box-sizing: border-box;
    font-family: Lora;
    text-align: center;
}

nav {
    h3 {
        color: black;
    }
    a {
        color: black;
        text-decoration: black;
        font-size: 17px;
    }
}

.product_main {
    width: 100%;
    margin: 30px 0;
}
.product_main_spot {
    width: 60vw;
    height: 50vh;
    margin: 0 auto;
    background-image: url(./product.jpg);
    background-size: cover;
    background-position: center;
    position: relative;
    h1 {
        position: absolute;
        bottom: -10%;
        left: 40%;
        font-size: 50px;
    }
}



.product_list {
    width: 70%;
    margin: 50px auto;
    figure {
        width: 33%;
        display: inline-block;
        figcaption {
            margin: 25px 0;
           
        }
    }
}
.product_img {
    margin: 0 auto;
    position: relative;
    width: 70%;
    overflow: hidden;
    img {
        width: 100%;
    }
    p {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        background-color: rgba(0,0,0,0.3);
        color: white;
        line-height: 300px;
        opacity: 0;
        &:hover {
            opacity: 1;
            transition: 1s;
        }
    }
   
}

.sequence {
    width: 90%;
    text-align: end;
    padding: 20px 0;
}

@media(min-width:480px) and (max-width:767px) {
    .product_main {
        margin: 0;
    }
    .product_main_spot {
        width: 100vw;
        height: 40vh;
        h1 {
            bottom: -15%;
            left: 40%;
        }
    }
    .product_list {
        width: 70%;
        margin: 50px auto;
        figure {
            width: 80%;
            margin: 15px 0;
            figcaption {
                margin: 5px 0;
            }
        }
    }
}

@media(min-width:768px) and (max-width:1024px) {
    .product_list {
        figure {
            width: 50%;
            figcaption {
                margin: 15px 0;
            }
        }
    }
}

