*{
  box-sizing: border-box;
  font-family: Lora;
}

@mixin main_button($color,$border) {
  width: 200px;
  padding: 7px;
  background-color: rgba(0,0,0,0.0);
  color: $color;
  border: $border;
  border-radius: 15px;
}
@mixin main_button_hover($background-color,$color) {
  background-color: $background-color;
  transition: 0.5s;
  color: $color;
  border: 1px solid rgba(0,0,0,0);
  width: 230px;
}


//main
.main {
  width: 100%;
  height: 100vh;
  background-image: url(./main.jpg);
  background-size: cover;
  background-position: center;
  h3 {
    color: white;
  }
  a {
    color: white;
    text-decoration: none;
    font-size: 17px;
  }
}
nav a {
  &:hover {
    color: grey;
  }
}

.main_text {
  width: 500px;
  margin-top: 200px;
  margin-left: 10%;
  margin-right: auto;
  color: white;
  p{
    font-size: 55px;
    line-height: 80px;
    margin-bottom: 50px;
    animation-name: main_text_ani;
    animation-duration: 1s;
    opacity: 1;
  }
  button {
    @include main_button(white,1px solid white );
    &:hover {
      @include main_button_hover(white,black);
    }
    &:hover::after {
      content: 'More';
      transition: 1s;
      margin-left: 5px;
    }
  }
}
@keyframes main_text_ani {
  from {
    opacity: 0;
   
  }
  to {
    opacity: 1;
 
  }
}



//tab
.tab {
  max-width: 100%;
  margin-top: 100px;
}
.tab_menu {
  text-align: center;
}
.tab_button {
  display: inline-block;
  margin: 0 40px;
  font-size: 18px;
  text-decoration: none;
  color: black;
  &:hover {
    animation-name: blink;
    animation-duration: 1s;
  }
}
@keyframes blink {
  0% {
    color: white;
  }
  100%{
    color: black;
  }
}

.tab_img { 
  width: 50%;
  margin: 30px auto;
  img {
    width: 200px;
    height: 270px;
    margin: 5px;
    float: left;
  }
}
.tab_text {
  width: 50%;
  display: inline-block;
  margin-top: 20px;
  margin-right: 100px;
  padding: 10px;
  p {
    margin: 25px 0;
    font-size: 15px;
  }
  button {
    @include main_button(black,1px solid rgb(73, 73, 73) );
    &:hover {
      @include main_button_hover(black,white);
    }
    &:hover::after {
      content: 'More';
      transition: 1s;
      margin-left: 5px;
    }
  }
}


//spot
.main_spot {
  width: 100%;
  margin-top: 100px;
  height: 70vh;
  background: url(./spot.jpg) fixed;
  background-size: cover;
  background-position: center;
  color: white;
  position: relative;
}
.main_spot_text {
  width: 300px;
  padding: 20px;
  position: absolute;
  bottom: 0;
  right: 0;
  h3 {
    margin: 15px 0;
  }
}


//product
.main_product {
  width: 100%;
  margin-top: 100px;
}
.main_product_img {
  text-align: center;
  display: inline-block;
  margin-top: 70px;
  figure {
    margin: 40px 50px;
    img {
      width: 50%;
      &:hover {
        transform: scale(1.1);
        transition: 0.5s;
      }
    }
    figcaption {
      margin: 20px;
      font-size: 13px;
    }  
  }
}


//footer
footer {
  width: 100%;
  margin-top: 50px;
  padding: 20px;
  text-align: center;
  font-size: 12px;
  div {
    display: inline-block;
    section {
      padding: 30px 100px;
      display: inline-block;
      vertical-align: top;
    }
    p {
      margin: 10px;
    }
  }
  a {
    font-size: 13px;
  }
}
.contact {
  margin-left: 300px;
}

.social {
  b {
    display: block;
  }
  img {
    width: 18px;
    margin: 15px 3px;
  }
}


@media(min-width:480px) and (max-width:767px){
  .main_text {
    width: 400px;
    p{
      font-size: 45px;
      line-height: 60px;
    }
  }
  .tab {
    max-width: 100%;
    margin-top: 20px;
  }
  .tab_button {
    margin: 0 40px;
    font-size: 15px;
  }
  .tab_img { 
    width: 50%;
    img {
      width: 100px;
      height: 150px;
    }
  }
  .tab_text {
    width: 100%;
    display: inline-block;
    margin-top: 10px;
    padding: 0px;
    p {
      margin: 15px 0;
      font-size: 12px;
    }
  } 
  .main_spot {
    margin-top: 50px;
    height: 50vh;
  }
  footer {
    margin-top: 10px;
    padding: 0px;
    font-size: 10px;
    div {
      section {
        padding: 10px 60px;
        display: block;
      }
    }
    a {
      font-size: 12px;
    }
  }
  .contact {
    margin-left: 0;
  }
}
@media (min-width:660px) and (max-width:767px) {
  .tab {
    margin-top: 50px;
  }
  .tab_img { 
    width: 50%;
    margin: 10px auto;
    img {
      width: 140px;
      height: 170px;
      margin: 5px;
    }
  }
  .tab_text {
    width: 80%;
    display: inline-block;
    margin-top: 10px;
    padding: 0px;
    margin: 0;
    p {
      margin: 15px 0;
      font-size: 13px;
    }
    button {
      padding: 3px;
    }
  }
}

@media(min-width:768px) and (max-width:1024px){
  /* 768~1024 */
  .tab {
    margin-top: 50px;
  }
  .tab_img { 
    width: 50%;
    margin: 10px auto;
    img {
      width: 120px;
      height: 150px;
      margin: 5px;
    }
  }
  .tab_text {
    width: 50%;
    display: inline-block;
    margin-top: 10px;
    padding: 0px;
    margin: 0;
    p {
      margin: 15px 0;
      font-size: 13px;
    }
    button {
      padding: 3px;
    }
  }
  .main_spot {
    margin-top: 70px;
    height: 40vh;
  }
  footer {
    margin-top: 50px;
    padding: 0px;
    font-size: 12px;
    div {
      section {
        padding: 8px 50px;
        display:inline-block;
      }
      .contact {
        margin-left: 0;
      }
    }
  }
}
@media (min-width:1025px) and (max-width:1599px){
  footer {
    div {
      display: inline-block;
      section {
        padding: 20px 50px;
        display: inline-block;
        vertical-align: top;
      }
      p {
        margin: 10px;
      }
    }
    a {
      font-size: 13px;
    }
  }
  .contact {
    margin-left: 100px;
  }

}
